* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  background-color: inherit;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  box-shadow: none;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
h1 {
  line-height: 1.2;
  color: var(--clr-fg-alt);
  font-size: 4rem;
}
h2 {
  line-height: 1.2;
  color: var(--clr-fg-alt);
  font-size: 1.6rem;
}
h3 {
  line-height: 1.2;
  color: var(--clr-fg-alt);
  font-size: 1.4rem;
}
h4 {
  line-height: 1.2;
  color: var(--clr-fg-alt);
  font-size: 1.3rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
button {
  cursor: pointer;
}
@media (max-width: 900px) {
  h1 {
    font-size: 2.6rem;
  }
}

@media (max-width: 600px) {
  h1 {
    font-size: 2.6rem;
  }

  h2 {
    font-size: 1rem;
  }
}
